import { I_Scene } from '@containers/Home/types';
import dynamic from 'next/dynamic';
import { memo, useCallback, useEffect, useState } from 'react';
import { useMobileDetect } from '@containers/Home/hook/useDetectMobile';
import { useSelector } from 'react-redux';
import {
  selectConfigCopyRight,
  selectIsMatterportProject,
  selectThemeConfigGlobal,
} from '@containers/Home/selectors';
import { ThemeWallWrapper } from './styled';

const Tour = dynamic(() => import('@containers/Home/components/Tour'), {
  ssr: false,
});
const ListScene = dynamic(() => import('./ListScene'), { ssr: false });
const Header = dynamic(() => import('./Header'));
const Copyright = dynamic(() => import('./Copyright'));
const Controlbar = dynamic(() => import('./Controlbar'));
const PostScene = dynamic(() => import('./PostScene'));

interface Props {
  onNextScene: () => void;
  onChangeScene: (scene: I_Scene) => void;
}

const ThemeWall = memo(({ onNextScene, onChangeScene }: Props) => {
  const [isShow, setIsShow] = useState(false);

  const { checkDevice } = useMobileDetect();

  const copyRight = useSelector(selectConfigCopyRight);
  const themeConfig = useSelector(selectThemeConfigGlobal);
  const isMatterportProject = useSelector(selectIsMatterportProject);

  const onToggleShow = useCallback(() => setIsShow(prev => !prev), []);

  useEffect(() => {
    if (themeConfig?.selected === 'wall')
      setIsShow(
        checkDevice().isMobile()
          ? !!themeConfig?.config?.autoOpenSceneListMobile
          : !!themeConfig?.config?.autoOpenSceneListDesktop,
      );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [themeConfig]);

  return (
    <ThemeWallWrapper className={isMatterportProject ? 'matterport' : ''}>
      <Header />

      <Tour onChangeScene={onChangeScene} onNextScene={onNextScene}>
        {({ krpanoRef }) => (
          <>
            <Controlbar
              isShow={isShow}
              krpanoRef={krpanoRef}
              onToggleShow={onToggleShow}
              className={copyRight?.isEnabled ? 'hasCopyRight' : ''}
            />
            <ListScene
              isShow={isShow}
              onChangeScene={onChangeScene}
              className={copyRight?.isEnabled ? 'hasCopyRight' : ''}
            />
          </>
        )}
      </Tour>

      <PostScene />
      <Copyright />
    </ThemeWallWrapper>
  );
});

export default ThemeWall;
